.customer__container {
  position: relative;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #84a178;
  border-radius: 50%;
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 0;
  left: 0;
  width: 100%;
  @include desktop {
  left: -100px;
  }
}

//-------------------------------------------------- SLIDER STYLES------------------------------------------
.customer {
  position: relative;
  padding-top: 80px;
  padding-bottom: 361px;
  background-image: url(../images/background/backgroud_mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @include retina {
    background-image: url(../images/background/backgroud_mobile@2x.jpg);
  }
  @include mobile {
    background-image: url(../images/background/background-tablet.jpg);

    @include retina {
      background-image: url(../images/background/background-tablet@2x.jpg);
    }
  }
  @include tablet {
    background-image: url(../images/background/background-dekstop.jpg);
padding-bottom: 228px;
padding-top: 100px;
    @include retina {
      background-image: url(../images/background/background-dekstop@2x.jpg);
    }
  }
  @include desktop {
    padding-top: 140px;
  }
}

.picture {
  border-radius: 50%;
  overflow: hidden;
}

.customer-reviews__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 32px;
}

.customer-reviews__text {
  padding: 35px 40px;
  font-weight: 400;
  font-size: 12px;
  line-height: 2.55;
  color: var(--fill-soc-hover-focus);
  text-align: center;

  background-color: var(--fill-soc-defaut);
  border-radius: 10px;

  @media screen and (min-width: 768px) {
    padding: 30px 80px;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
  }
  @media screen and (min-width: 1200px) {
    max-width: 968px;
    padding: 43px 110px;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.86;
  }
}

.customer-reviews__name {
  font-weight: 500;
  font-size: 20px;
  line-height: 186%;
  margin-bottom: 20px;
  color: #131313;
  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.slick-track {
  display: flex;
  align-items: flex-start;
}

.slick-list {
  margin-bottom: 12px;
  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 32px;
  }
}
